@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
* {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
}
body .container {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1440px;
  padding-top: 6rem;
}
body .img-wrapper {
  display: flex;
  background-color: aqua;
}
body .img-wrapper img {
  width: 100%;
  max-width: 505px;
}
body .content {
  max-width: 380px;
  margin-left: 95px;
}
body .content .title {
  font-weight: 700;
  font-size: 2.625rem;
  line-height: 120%;
  color: #121212;
}
body .content .description {
  color: #121212;
  margin-top: 1.5rem;
  line-height: 150%;
}
body .content .button {
  background: #E4C1AE;
  margin-top: 2rem;
  color: white;
  border-radius: 2rem;
  border: 0;
  font-weight: bold;
  font-size: 1rem;
  height: 3rem;
  width: 8rem;
  padding: 1rem, 2.5rem;
}